.main {
  background: #0C1321;
  padding: 125px 0 180px;
  color: #fff;
  overflow: hidden;
  &__inner {
    display: flex;
    align-items: flex-end;
    gap: 12px;
  }
  &__desc {
    position: relative;
    z-index: 2;
    width: 50%;
  }
  &__img {
    position: relative;
    width: 50%;
    &:after {
      content: '';
      position: absolute;
      right: 0;
      bottom: -35px;
      width: 694px;
      height: 694px;
      background: #212F4A;
      filter: blur(200px);
      border-radius: 50%;
      pointer-events: none;
    }
    img:nth-child(1) {
      position: relative;
      z-index: 2;
      width: 100%;
    }
    img:nth-child(2) {
      position: absolute;
      z-index: 1;
      right: -160px;
      bottom: -190px;
      max-width: 1660px;
      pointer-events: none;
    }
  }
  &__subtitle {
    font-size: 20px;
    line-height: 105%;
    letter-spacing: -0.6px;
    color: rgba(255, 255, 255, 0.7);
  }
  &__title {
    font-size: 60px;
    line-height: 105%;
    font-weight: 700;
    letter-spacing: -1.8px;
    margin-top: 5px;
  }
  &__text {
    margin-top: 30px;
    p {
      font-size: 16px;
      line-height: 140%;
      letter-spacing: -0.32px;
      color: rgba(255, 255, 255, 0.7);
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 30px;
    margin-top: 40px;
  }
  &__group-mob {
    display: none;
  }
  &__btn {
    height: 48px;
    padding: 5px 50px;
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    letter-spacing: -0.32px;
    color: rgba(255, 255, 255, 0.7);
  }
}

@media screen and (max-width: 1150px) {
  .main {
    padding: 100px 0 160px;
  }
}

@media screen and (max-width: 992px) {
  .main {
    padding: 40px 0 60px 0;
    &__inner {
      flex-direction: column;
      align-items: start;
      gap: 20px;
    }
    &__desc {
      width: 100%;
    }
    &__img {
      max-width: 500px;
      width: 100%;
      margin: 0 auto;
      &:after {
        right: 50%;
        bottom: -112px;
        transform: translate(50%, 0);
      }
    }
    &__subtitle {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
    &__title {
      font-size: 34px;
      letter-spacing: -1.02px;
    }
    &__text {
      margin-top: 20px;
      p {
        font-size: 14px;
        letter-spacing: -0.28px;
      }
    }
    &__group {
      display: none;
    }
    &__group-mob {
      position: relative;
      z-index: 2;
      display: flex;
      flex-direction: column;
      align-items: start;
      gap: 20px;
      margin-top: 20px;
    }
    &__info {
      font-size: 14px;
      letter-spacing: -0.28px;
      br {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    padding-bottom: 80px;
    &__text {
      p {
        br {
          display: none;
        }
      }
    }
    &__img {
      max-width: 330px;
      img:nth-child(2) {
        right: -90px;
        bottom: -100px;
        max-width: 910px;
      }
    }
    &__group-mob {
      width: 100%;
    }
    &__btn {
      width: 100%;
      padding: 5px 20px;
    }
  }
}