.header {
  position: relative;
  z-index: 12;
  background: #0C1321;
  padding: 27px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 34px;
  }
  &__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 47px;
  }
  &__logo {
    width: 213px;
    display: block;
    img {
      width: 100%;
    }
  }
  .menu {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    &-item a {
      position: relative;
      font-size: 16px;
      line-height: 105%;
      letter-spacing: -0.32px;
      color: rgba(255, 255, 255, 0.7);
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -15px;
        width: 0;
        height: 2px;
        background: #FFC413;
        transition: all 0.15s;
      }
      &:hover {
        color: #fff;
        &:after {
          width: 60%;
        }
      }
    }
    &-item--active a {
      color: #fff;
      &:after {
        width: 60%;
      }
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
  }
  &__phone {
    font-size: 20px;
    line-height: 105%;
    letter-spacing: -0.4px;
    color: #fff;
    &:hover {
      color: rgba(255, 255, 255, 0.7);
    }
  }
  &__btn {
    background: transparent;
    padding: 0;
    font-size: 13px;
    line-height: 105%;
    letter-spacing: -0.26px;
    color: #FFC413;
    &:hover {
      color: rgba(255, 196, 19, 0.7);
    }
  }
  &__mob {
    position: relative;
    z-index: 12;
    display: none;
    align-items: center;
    gap: 20px;
  }
  &__phone-mob {
    width: 42px;
    height: 42px;
    border: 2px solid #fff;
    border-radius: 50%;
    &:hover {
      border-color: rgba(255, 255, 255, 0.7);
      svg path {
        fill: rgba(255, 255, 255, 0.7);
      }
    }
    svg path {
      transition: all 0.3s;
    }
  }
}
.burger {
  position: relative;
  width: 42px;
  height: 42px;
  border: 2px solid #FFC413;
  border-radius: 50%;
  cursor: pointer;
  span {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    width: 16px;
    height: 2px;
    background: #FFC413;
    border-radius: 3px;
    transition: all 0.5s;
  }
  span:nth-child(1) {
    top: 13px;
  }
  span:nth-child(2) {
    transform: translate(-50%, -50%);
  }
  span:nth-child(3) {
    bottom: 15px;
  }
  &--active {
    span:nth-child(1) {
      top: 18px;
      transform: translate(-50%, 0) rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 18px;
      transform: translate(-50%, 0) rotate(45deg);
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__inner {
      gap: 24px;
    }
    &__right {
      gap: 24px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .header {
    &__logo {
      width: 180px;
    }
    &__phone {
      font-size: 16px;
      letter-spacing: -0.32px;
    }
  }
}

@media screen and (max-width: 1080px) {
  .header {
    .menu {
      &-item a {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding: 25px 0;
    &__left {
      position: relative;
      z-index: 12;
    }
    &__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      flex-direction: column;
      gap: 30px;
      background: #0C1321;
      padding: 120px 30px 40px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
      overflow-y: auto;
      &--active {
        opacity: 1;
        pointer-events: auto;
      }
    }
    .menu {
      flex-grow: unset;
      flex-direction: column;
      align-items: start;
      gap: 30px;
      &-item a {
        font-size: 16px;
        &:after {
          display: none;
        }
      }
    }
    &__group {
      width: 100%;
    }
    &__phone {
      font-size: 20px;
    }
    &__mob {
      display: flex;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 155px;
    }
  }
}